.col {
	&--half {
		@include make-column(5);
		@include make-gutter(0,1);
		&:last-of-type {
			@include make-gutter(1,0); }
		@include display-for(sm xs) {
			@include make-column(12);
			@include make-gutter(0, 0);
			&:last-of-type {
				@include make-gutter(0,0); } } }
	&--half-left {
		@include make-column(5);
		@include make-gutter(1,0);
		&:last-of-type {
			@include make-gutter(0,1); }
		@include display-for(sm xs) {
			@include make-column(12);
			@include make-gutter(0, 0);
			&:last-of-type {
				@include make-gutter(0,0); } } }
	&--half-small {
		@include make-column(5.75);
		@include make-gutter(0, 0.25);
		&:last-of-type {
			@include make-gutter(0.25, 0); } } }

