@mixin container {
	// +make-adaptive
	box-sizing: border-box;
	position: relative;
	width: 90%;
	max-width: 1200px;
	margin: 0 auto;
	@include display-for(xl lg md) {
		margin: 0 auto; }
	@include display-for(xs sm) {
		margin: 0;
		padding: 0 12px; } }
.group, .container {
	@include container; }
.flex-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 60px 0; }
