$grid-base: 12;

@mixin make-column($columns, $grid: $grid-base) {
	box-sizing: border-box;
	width: percentage($columns / $grid); }
@mixin make-gutter($left-gutter: 0, $right-gutter: 0, $grid: $grid-base) {
	margin-left: percentage($left-gutter / $grid);
	margin-right: percentage($right-gutter / $grid); }
@mixin inline-block {
	display: inline-block;
	vertical-align: top; }
