@import "layout/make-responsive";
@import "layout/grid";
@import "layout/make-adaptive";
@import "container";

@mixin contain-col {
	width: 100%;
	@include display-for(xl) {
		max-width: 573px; }
	@include display-for(lg) {
		max-width: 468px; }
	@include display-for(md) {
		max-width: 372px; }
	@include display-for(sm) {
		max-width: 290px; } }

.contain-content {
	&--left {
		line-height: 28px;
		@include contain-col;
		float: right;
		padding-right: 50px;
		@include display-for(sm xs) {
			max-width: 1000px;
			padding-right: 0;
			float: none;
			@include make-column(12);
			@include container; } }
	&--right {
		line-height: 28px;
		@include contain-col;
		padding-left: 50px;
		@include display-for(sm xs) {
			padding-left: 0;
			max-width: 1000px;
			@include make-column(12);
			@include container; } } }


