@import "make-responsive";
@import "grid";
@import "make-adaptive";
@import "col-gutter";
@import "blog";
@import "normalize";
.col {
	&-full {
		@include make-column(12);
		@include display-for(sm xs); }
	&-half {
		@include make-column(6);
		@include display-for(sm xs) {
			@include make-column(12); } }
	&-third {
		background: cyan;
		@include make-column(4);
		@include display-for(sm) {
			@include make-column(6);
			&:nth-child(3n) {
				@include make-column(12); } }
		@include display-for(xs) {
			@include make-column(12); } }
	&-fourth {
		@include make-column(3);
		@include display-for(md sm) {
			@include make-column(6); }
		@include display-for(xs) {
			@include make-column(12); } }
	&-sixth {
		@include make-column(2);
		@include display-for(md) {
			@include make-column(4); }
		@include display-for(sm xs) {
			@include make-column(12); } } }

.row {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	display: -webkit-flex;
	-webkit-flex-direction: row;
	-webkit-flex-wrap: wrap; }
.col-third {
	background: papayawhip;
	&:nth-child(2n) {
		background: cyan; } }
.col-sixth {
	background: palegoldenrod;
	&:nth-child(2n) {
		background: lime; } }



